@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
body {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

  .wrapper {
	height: 300px;
	width: 300px;
  }

  .base {
	background-color: #ecf0f1;
	width: 200px;
	height: 170px;
	border-radius: 30px;
	margin: 0 auto;
	position: relative;
	top: 100px;
	z-index: 100;
  }

  .base-bottom {
	background-color: #bdc3c7;
	width: 200px;
	height: 85px;
	border-radius: 0 0 30px 30px;
	top: 85px;
	position: relative;
	opacity: 0.4;
  }

  .lock-cirlce {
	height: 180px;
	width: 110px;
	border-radius: 45px;
	z-index: 10;
	background-color: #bdc3c7;
	position: relative;
	margin: 0 auto;
	bottom: 150px;
	-webkit-animation: unlock-circle 6s forwards;
	        animation: unlock-circle 6s forwards;
  }

  .lock-circle-inside {
	height: 180px;
	width: 50px;
	border-radius: 30px;
	z-index: 20;
	background-color: #000;
	position: relative;
	margin: 0 auto;
	top: 25px;
  }

  .lock-box {
	-webkit-animation: unlock-box 6s forwards;
	        animation: unlock-box 6s forwards;
	position: relative;
	height: 50px;
	width: 50px;
	background-color: #000;
	bottom: 230px;
	left: 170px;
	z-index: 80;
  }

  .lock-inside-top {
	width: 50px;
	height: 50px;
	border-radius: 50px;
	background-color: black;
	z-index: 300;
	position: relative;
	bottom: 45px;
	left: 75px;
  }

  .lock-inside-bottom {
	width: 30px;
	height: 80px;
	background-color: black;
	z-index: 300;
	position: relative;
	bottom: 85px;
	left: 85px;
  }

  .preloader-2 {
	display: flex;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	align-items: center;
  }

  .preloader-2 .loading {
	font-size: 18px;
	margin: 0 10px;
  }

  .preloader-2 .loading-active {
	font-size: 18px;
	margin: 0 10px;
	color: green;
  }

  .preloader-2 .line {
	width: 1px;
	height: 12px;
	background: #fff;
	margin: 0 1px;
	display: inline-block;
	-webkit-animation: opacity-2 1000ms infinite ease-in-out;
	        animation: opacity-2 1000ms infinite ease-in-out;
  }

  .preloader-2 .line-1 { -webkit-animation-delay: 800ms; animation-delay: 800ms; }
  .preloader-2 .line-2 { -webkit-animation-delay: 600ms; animation-delay: 600ms; }
  .preloader-2 .line-3 { -webkit-animation-delay: 400ms; animation-delay: 400ms; }
  .preloader-2 .line-4 { -webkit-animation-delay: 200ms; animation-delay: 200ms; }
  .preloader-2 .line-6 { -webkit-animation-delay: 200ms; animation-delay: 200ms; }
  .preloader-2 .line-7 { -webkit-animation-delay: 400ms; animation-delay: 400ms; }
  .preloader-2 .line-8 { -webkit-animation-delay: 600ms; animation-delay: 600ms; }
  .preloader-2 .line-9 { -webkit-animation-delay: 800ms; animation-delay: 800ms; }

  @-webkit-keyframes opacity-2 {
	0% {
	  opacity: 1;
	  height: 15px;
	}
	50% {
	  opacity: 0;
	  height: 12px;
	}
	100% {
	  opacity: 1;
	  height: 15px;
	}
  }

  @keyframes opacity-2 {
	0% {
	  opacity: 1;
	  height: 15px;
	}
	50% {
	  opacity: 0;
	  height: 12px;
	}
	100% {
	  opacity: 1;
	  height: 15px;
	}
  }


  @-webkit-keyframes unlock-circle {
	0% {
		bottom: 150px;
	}
	25% {
		bottom: 150px;
	}
	50% {
		bottom: 200px;
	}
	75% {
		bottom: 200px;
	}
	100% {
		bottom: 200px;
	}
  }


  @keyframes unlock-circle {
	0% {
		bottom: 150px;
	}
	25% {
		bottom: 150px;
	}
	50% {
		bottom: 200px;
	}
	75% {
		bottom: 200px;
	}
	100% {
		bottom: 200px;
	}
  }

  @-webkit-keyframes unlock-box {
	0% {
		bottom: 230px;
	}
	25% {
		bottom: 230px;
	}
	50% {
		bottom: 280px;
	}
	75% {
		bottom: 280px;
	}
	100% {
		bottom: 280px;
	}
  }

  @keyframes unlock-box {
	0% {
		bottom: 230px;
	}
	25% {
		bottom: 230px;
	}
	50% {
		bottom: 280px;
	}
	75% {
		bottom: 280px;
	}
	100% {
		bottom: 280px;
	}
  }

.editor {
	display: flex;
	width: 100%;
	height: 100%;
	background: #000;
	justify-content: center;
	align-items: center;
}

.editor .item {
	flex-basis: 50%;
	position: relative;
}

.editorBar {
	position: absolute;
	width: 30px;
	/* border-right: 1px solid #263238; */
	height: 100%;
}

.codeHolder {
	margin: 20px;
    width: calc(100% - 40px);
    background-image: url(/cv/static/media/paper.1b83c24e.jpg);
    height: calc(100% - 40px);
}

.itemHolder {
	min-height: 20px;
    display: flex;
    margin: 5px;
    border-radius: 4px;
    padding: 5px;
}

.itemLabel {
	color: #000;
	font-size: 16px;
	margin-right: 5px;
}

.title {
	font-size: 20px;
    background: black;
    text-align: center;
}

.itemContent {
	color: #000;
	font-size: 16px;
	font-family: 'Indie Flower';
}

.container {
	transition: 1s;
	max-width: 500px;
	width: 100%;
	min-height: 750px;
	display: flex;
    justify-content: center;
	background-image: url(/cv/static/media/cover.c83342e6.png);
	position: relative;
    border-radius: 5px;
}

.container:hover {
	margin-left: 500px;
	transition: 1s;
}

.container::before {
	content: '';
    position: absolute;
    width: calc(100% - 40px);
    height: 30px;
    left: 59px;
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
    background-image: url(/cv/static/media/paper.1b83c24e.jpg);
}


.container:hover .cover{
	-webkit-transform: rotateY(-180deg);
	        transform: rotateY(-180deg);
}

.image {
	margin: 20px;
    width: calc(100% - 40px);
    background-image: url(/cv/static/media/paper.1b83c24e.jpg);
	height: calc(100% - 40px);
	-webkit-transform: rotate(3deg);
	        transform: rotate(3deg);
    position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.image > img {
	position: relative;
	display: flex;
	align-items: center;
	-webkit-transform: rotate(3deg);
	        transform: rotate(3deg);
    border-radius: 50%;
}

.dangerHolder {
	display: flex;
	justify-content: center;
}

.cover {
	width: 100%;
	height: 100%;
	position: absolute;
	background-image: url(/cv/static/media/cover.c83342e6.png);
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	-webkit-transform-origin: left;
	        transform-origin: left;
	transition: all .5s ease-in;
    border-radius: 5px;
}

.front {
	position: absolute;
	width: 100%;
	height: 100%;
	background-image: url(/cv/static/media/cover.c83342e6.png);
	border: 1px solid #141823;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	display: flex;
    justify-content: center;
	align-items: center;
	border-radius: 5px;
	z-index: 1;
}

.back {
	position: absolute;
	width: 100%;
	height: 100%;
	background: #141823;
	background-image: url(/cv/static/media/cover.c83342e6.png);
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
	border-radius: 5px;
	z-index: 2;
}

.stamp-text {
	color:red;
  	display:inline-block;
  	-webkit-transform:rotate(-5deg);
  	        transform:rotate(-5deg);
	border:10px solid red;
	-webkit-mask-size: cover;
	        mask-size: cover;
	-webkit-mask-image: url("https://cdn.pixabay.com/photo/2018/04/27/21/39/earth-3355931_960_720.jpg");
	        mask-image: url("https://cdn.pixabay.com/photo/2018/04/27/21/39/earth-3355931_960_720.jpg");
}

.agent-stamp-text {
	position: absolute;
	color:red;
  	display:inline-block;
  	-webkit-transform:rotate(-5deg);
  	        transform:rotate(-5deg);
	border:10px solid red;
	top: 60px;
    opacity: 0.5;
}

.technologiesWrapper {
	display: flex;
	flex-direction: column;
}

.technologies {
	display: flex;
    flex-direction: column;
}

.technologiesList {
	display: flex;
    flex-wrap: wrap;
}

.responsiveContainer {
	width: 500px !important;
	height: 560px !important;
	background-image: url(/cv/static/media/cover.c83342e6.png);
}

.responsiveCover {
	background-image: url(/cv/static/media/cover.c83342e6.png);
	position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.stamp-text {
	font-size: 45px;
	color:red;
  	display:inline-block;
  	-webkit-transform:rotate(-5deg);
  	        transform:rotate(-5deg);
	border:10px solid red;
	-webkit-mask-size: cover;
	        mask-size: cover;
	-webkit-mask-image: url("https://cdn.pixabay.com/photo/2018/04/27/21/39/earth-3355931_960_720.jpg");
	        mask-image: url("https://cdn.pixabay.com/photo/2018/04/27/21/39/earth-3355931_960_720.jpg");
}

.responsivePage {
	background-image: url(/cv/static/media/paper.1b83c24e.jpg);
	position: absolute;
    width: 100%;
    height: 100%;
}

.responsiveImage {
	margin: 20px;
    width: calc(100% - 40px);
	height: calc(100% - 40px);
    position: absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.responsiveImage > img {
	position: relative;
	display: flex;
	align-items: center;
    border-radius: 50%;
}

@media(max-width: 500px) {
	.responsiveContainer {
		width: 100% !important;
		height: 100% !important;
	}
}


.App {
  background: #000;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

